import * as Yup from "yup";
import "./locale";

Yup.addMethod(Yup.string, "passStrong", function () {
  return this.test(
    "passStrong",
    "半角英数記号で入力してください",
    function (value) {
      if (value == null || value === "") return true;
      return value.match(
        /^[0-9a-zA-Z`~!@#\$%\^&\*\(\)_\+\-=\{\}\[\]\\\|:;"'<>,\.\?\/]*$/
      );
    }
  );
});

Yup.addMethod(Yup.string, "halfNumber", function () {
  return this.test(
    "halfNumber",
    "半角数字で入力してください",
    function (value) {
      if (value == null || value === "") return true;
      return value.match(/^[0-9]+$/);
    }
  );
});

Yup.addMethod(Yup.string, "postalcode", function () {
  return this.test(
    "postalcode",
    "7桁の数字で入力してください",
    function (value) {
      if (value == null || value === "") return true;
      return value.match(/^[0-9]{3}[0-9]{4}$/);
    }
  );
});

Yup.addMethod(Yup.string, "zenkaku_katakana", function () {
  return this.test(
    "zenkaku_katakana",
    "全角カタカナで入力してください",
    function (value) {
      if (value == null || value === "") return true;
      return value.match(/^[ァ-ンヴー]+$/);
    }
  );
});

export default Yup;
